import React from 'react'
import { Input, Select } from '@vkontakte/vkui'
import Div from '@vkontakte/vkui/dist/components/Div/Div'
import List from '@vkontakte/vkui/dist/components/List/List'
import CheckBox from '@vkontakte/vkui/dist/components/CheckBox/CheckBox'
import ComItem from './ComItem'

class Post extends ComItem {
    constructor(props) {
        super(props)
        this.changeCheckBox = this.changeCheckBox.bind(this)
    }

    changeCheckBox(e) {
        const n = e.target.name
        const v = e.target.checked
        this.props.cc.setState(ps => {
            this.state.item.withPinned = v
            return ps
        })
    }

    fields() {
        return (
            <List>
                <Div>
                    <Select placeholder="Выберите тип" value={this.state.item.type} onChange={this.selectItemType}>
                        <option value="reference">По ссылке</option>
                        <option value="pinned">Закрепленный</option>
                        <option value="number">По порядковому номеру</option>
                    </Select>
                </Div>
                <Div hidden={this.setFieldHidden(['reference'])}>
                    <Input type="text" placeholder="Ссылка на пост" name="reference" value={this.state.item.reference} onChange={this.changeTextField} />
                </Div>
                <Div hidden={this.setFieldHidden(['number', 'pinned'])}>
                    <Input type="text" placeholder="Ссылка на владельца" name="owner" value={this.state.item.owner} onChange={this.changeTextField} />
                </Div>
                <Div hidden={this.setFieldHidden(['number'])}>
                    <Input type="text" placeholder="Порядковый номер" name="number" value={this.state.item.number} onChange={this.changeField} />
                </Div>
                <Div hidden={this.setFieldHidden(['number'])}>
                    <CheckBox name='withPinned' checked={this.state.item.withPinned} onChange={this.changeCheckBox}>Учитывать закрепленный пост</CheckBox>
                </Div>
            </List>
        )
    }
}

export default Post
