import React from 'react'
import { Input, Select } from '@vkontakte/vkui'
import Div from '@vkontakte/vkui/dist/components/Div/Div'
import Button from '@vkontakte/vkui/dist/components/Button/Button'
import List from '@vkontakte/vkui/dist/components/List/List'
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell'
import Group from '@vkontakte/vkui/dist/components/Group/Group'
import Icon24Add from '@vkontakte/icons/dist/24/add'
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel'
import ComItem from './ComItem'

class Media extends ComItem {
    constructor(props) {
        super(props)
        this.getIdsMap = this.getIdsMap.bind(this)
        this.addId = this.addId.bind(this)
        this.deleteId = this.deleteId.bind(this)
        this.changeId = this.changeId.bind(this)
        this.checkIdsCount = this.checkIdsCount.bind(this)
    }

    selectItemType(e) {
        super.selectItemType(e)
        if (e.target.value === 'reference') {
            this.props.cc.setState(ps => {
                this.state.item.ids = ['']
                return ps
            })
        } else {
            this.props.cc.setState(ps => {
                this.state.item.ids = []
                return ps
            })
        }

        if (e.target.value != 'id') {
            this.props.cc.setState(ps => {
                this.state.item.albumIds = ['']
                return ps
            })
        } else {
            this.props.cc.setState(ps => {
                this.state.item.albumIds = []
                return ps
            })
        }
    }

    changeId(e) {
        const n = e.currentTarget.name
        const v = e.currentTarget.value
        const field = e.currentTarget.dataset.field
        this.props.cc.setState(ps => {
            this.state.item[field][n] = v
            return ps
        })
    }

    addId(e) {
        const field = e.currentTarget.dataset.field
        this.props.cc.setState(ps => {
            this.state.item[field].push('')
            return ps
        })
    }

    deleteId(e) {
        const field = e.currentTarget.dataset.field
        const index = e.currentTarget.dataset.index
        this.props.cc.setState(ps => {
            this.state.item[field].splice(index, 1)
            return ps
        })
    }

    checkIdsCount() {
        var count = 0
        this.props.cc.idsItems.forEach(item => count += item.ids.length)
        return count < 10
    }

    getIdsMap(field, str, types) {
        return (
            <Group hidden={this.setFieldHidden(types)}>
                {this.state.item[field].map((id, i) => (
                    <Cell asideContent={<Icon24Cancel hidden={this.state.item[field].length < 2} data-index={i} data-field={field} onClick={this.deleteId} />}>
                        <Input
                            name={i}
                            value={id}
                            type="text"
                            placeholder={str}
                            value={this.state.item[field][i]}
                            data-field={field}
                            onChange={this.changeId}
                        />
                    </Cell>))}
                <Div>
                    <Button
                        size="s"
                        before={<Icon24Add />}
                        disabled={field == 'ids' ? !this.checkIdsCount() : false}
                        data-field={field}
                        onClick={this.addId}
                    >Добавить</Button>
                </Div>
            </Group>
        )
    }

    fields() {
        return (
            <List>
                <Div>
                    <Select placeholder="Выберите тип" value={this.state.item.type} onChange={this.selectItemType}>
                        <option value="reference">По ссылке</option>
                        <option value="random">Случайное</option>
                        <option value="number">По номеру</option>
                        <option value="all">Все содержимое альбома</option>
                    </Select>
                </Div>
                <Div hidden={this.setFieldHidden(['number', 'all'])}>
                    <Input type="text" placeholder="Ссылка на альбом" name={0} data-field='albumIds' value={this.state.item.albumIds[0]} onChange={this.changeId} />
                </Div>
                <Div hidden={this.setFieldHidden(['number'])}>
                    <Input type="text" placeholder="Номер медиавложения" name='number' value={this.state.item.number} onChange={this.changeField} />
                </Div>
                {this.getIdsMap('ids', 'Ссылка на медиавложение', ['reference'])}
                {this.getIdsMap('albumIds', 'Ccылка на альбом', ['random'])}
            </List>
        )
    }
}

export default Media
