import React from 'react'
import { platform, IOS, Select } from '@vkontakte/vkui'
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel'
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader'
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton'
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back'
import Icon24Back from '@vkontakte/icons/dist/24/back'
import { FormLayout, FormLayoutGroup, Input } from '@vkontakte/vkui'
import Div from '@vkontakte/vkui/dist/components/Div/Div'
import Button from '@vkontakte/vkui/dist/components/Button/Button'
import Group from '@vkontakte/vkui/dist/components/Group/Group'
import Alert from '@vkontakte/vkui/dist/components/Alert/Alert'
import Header from '@vkontakte/vkui/dist/components/Header/Header'
import Separator from '@vkontakte/vkui/dist/components/Separator/Separator'
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell'
import Icon24Add from '@vkontakte/icons/dist/24/add'
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel'
import ComItemButton from './ComItemButton'
import Text from './Text'
import Post from './Post'
import Media from './Media'
import BotApi from '../../BotApi'

const osName = platform()

class ComPanel extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            id: 0,
            comName: '',
            priority: '',
            communities: [],
            text: {
                name: 'text',
                rusName: 'текст',
                status: false,
                text: '',
            },
            post: {
                name: 'post',
                rusName: 'пост',
                status: false,
                type: null,
                withPinned: false,
                owner: '',
                id: '',
                number: '',
                reference: '',
            },
            image: {
                name: 'image',
                rusName: 'изображение',
                status: false,
                type: null,
                owner: '',
                number: '',
                ids: [],
                albumIds: [],
            },
            video: {
                name: 'video',
                rusName: 'видео',
                status: false,
                type: null,
                owner: '',
                number: '',
                ids: [],
                albumIds: [],
            }
        }

        this.idsItems = [this.state.image, this.state.video]

        this.changeName = this.changeName.bind(this)
        this.validateEmpty = this.validateEmpty.bind(this)
        this.getAlert = this.getAlert.bind(this)
        this.validateStatus = this.validateStatus.bind(this)
        this.validate = this.validate.bind(this)
        this.deleteCommand = this.deleteCommand.bind(this)
        this.changeField = this.changeField.bind(this)
        this.selectCommunity = this.selectCommunity.bind(this)
        this.getCommunities = this.getCommunities.bind(this)
        this.addCommunity = this.addCommunity.bind(this)
        this.deleteCommunity = this.deleteCommunity.bind(this)

        this.communsList = BotApi.getAvailableCommunities(props.userId)
    }

    changeName(e) {
        const v = e.currentTarget.value
        this.setState(ps => {
            if (!v.includes(' ') && !v.includes('\n')) {
                this.state.comName = v.toLowerCase()
            }
            return ps
        })
    }

    validateStatus() {
        const arr = [
            this.state.text,
            this.state.post,
            this.state.image,
            this.state.video,
        ]
        for (var a in arr) {
            const aa = arr[a]
            if (aa.status) {
                return true
            }
        }
        return false
    }

    validatePostEmpty(arr, item) {
        if (item.status) {
            if (item.type != 'reference') {
                arr.push(item.owner)
            }
            switch (item.type) {
                case 'reference':
                    arr.push(item.reference)
                    break
                case 'number': arr.push(item.number)
                    break
            }
        }
    }

    validateMediaEmpty(arr, item) {
        if (item.status) {
            if (item.type != 'reference') {
                for (var a in item.albumIds) {
                    arr.push(item.albumIds[a])
                }
            }
            switch (item.type) {
                case 'reference':
                    for (var a in item.ids) {
                        arr.push(item.ids[a])
                    }
                    break
                case 'number':
                    arr.push(item.number)
                    break
            }
        }
    }

    validateEmpty() {
        const arr = []
        if (this.state.text.status) {
            arr.push(this.state.text.text)
        }
        this.validatePostEmpty(arr, this.state.post)
        this.validateMediaEmpty(arr, this.state.image)
        this.validateMediaEmpty(arr, this.state.video)
        for (var a in arr) {
            const aa = arr[a]
            if (aa === null || aa.trim() === '') {
                return false
            }
        }
        for (var a in this.state.communities) {
            const aa = this.state.communities[a]
            if (aa === '') {
                return false
            }
        }
        return true
    }

    getAlert(text) {
        return (
            <Alert
                actions={[{
                    title: 'ОК',
                    mode: 'cancel',
                    autoclose: true,
                }]}
                onClose={() => this.props.app.setPopout(null)}
            >
                <p>{text}</p>
            </Alert>
        )
    }

    validate() {
        const error = text => this.props.app.setPopout(this.getAlert(text))

        if (this.state.comName == null || this.state.comName.trim() == '') {
            return error('Название команды не может быть пустым!')
        }
        if (!this.validateStatus()) {
            return error('Ответ команды не может быть пустым!')
        }
        if (!this.validateEmpty()) {
            return error('Не все поля заполнены!')
        }

        this.prepareCommandBody()
    }

    preparePostData(item) {
        if (item.type === 'reference') {
            var str = item.reference
            var m = str.match(/vk.com\/wall.\d+_\d+$/)
            if (m == null) {
                throw new Error('Поле со ссылкой на пост имеет неверный формат!')
            }
            str = str.replace(/^.*vk.com\/wall/, '')
            var arr = str.split('_')
            item.owner = arr[0]
            item.id = arr[1]
        }

        if (item.type === 'pinned' || item.type === 'number') {
            var str = item.owner
            var m = str.match(/vk\.com\//)
            if (m == null) {
                throw new Error('Поле со ссылкой на владельца имеет неверный формат!')
            }
            str = str.replace(/^.*vk\.com\//, '')
            var resp = BotApi.vk(`groups.getById?group_id=${str}`)
            if (resp == undefined) {
                var resp2 = BotApi.vk(`users.get?user_ids=${str}`)
                if (resp2 == undefined) {
                    throw new Error('Ошибка обработки ссылки на владельца!')
                } else {
                    item.owner = resp2[0].id + ''
                }
            } else {
                item.owner = '-' + resp[0].id
            }
        }
    }

    prepareMediaData(item) {
        if (item.type === 'reference') {
            var testOwner = null
            var refs = []
            for (var i in item.ids) {
                var str = item.ids[i]
                var m = item.name === 'image' ? str.match(/vk\.com\/photo.\d+_\d+$/) : str.match(/vk\.com\/video.\d+_\d+$/)
                if (m == null) {
                    throw new Error('Поле со ссылкой на медиавложение имеет неверный формат!')
                }
                str = item.name === 'image' ? str.replace(/^.*vk\.com\/photo/, '') : str.replace(/^.*vk\.com\/video/, '')
                var arr = str.split('_')
                if (testOwner != null && testOwner != arr[0]) {
                    throw new Error('Все медиавложения должны принадлежать одному владельцу!')
                }
                testOwner = arr[0]
                refs.push(arr[1])
            }
            item.owner = testOwner
            item.ids = refs
        }

        if (item.type === 'random' || item.type === 'number' || item.type === 'all') {
            var testOwner = null
            var refs = []
            for (var i in item.albumIds) {
                var str = item.albumIds[i]
                var m = str.match(/vk\.com\/album.\d+_\d+$/)
                if (m == null) {
                    throw new Error('Поле со ссылкой на альбом имеет неверный формат!')
                }
                str = str.replace(/^.*vk\.com\/album/, '')
                var arr = str.split('_')
                if (testOwner != null && testOwner != arr[0]) {
                    throw new Error('Все альбомы должны принадлежать одному владельцу!')
                }
                testOwner = arr[0]
                refs.push(arr[1])
            }
            item.owner = testOwner
            item.albumIds = refs
        }
    }

    prepareCommandBody() {
        const error = text => this.props.app.setPopout(this.getAlert(text))
        const prepareInt = a => Number(a.trim() == '' ? '0' : a)
        const prepareArray = a => a.map(id => id == null || id === '' ? '' : id).join(' ')
        const isExist = a => a.status && a.type != null && a.type.length > 0

        try {
            this.preparePostData(this.state.post)
            this.prepareMediaData(this.state.image)
            this.prepareMediaData(this.state.video)
        } catch (err) {
            error(err.message)
            return
        }

        var body = {
            id: this.state.id == 0 ? undefined : this.state.id,
            name: this.state.comName,
            text: this.state.text.status ? this.state.text.text : null,
            priority: prepareInt(this.state.priority),
            communityLinks: this.state.communities.map(c => {
                return { community: { id: c } }
            }),
            author: {
                id: prepareInt(this.props.userId),
            },
            post: isExist(this.state.post) ? {
                id: this.state.post.objId,
                type: this.state.post.type,
                ownerId: prepareInt(this.state.post.owner),
                postId: prepareInt(this.state.post.id),
                number: prepareInt(this.state.post.number),
                withPinned: this.state.post.withPinned,
            } : null,
            image: isExist(this.state.image) ? {
                id: this.state.image.objId,
                type: this.state.image.type,
                ownerId: prepareInt(this.state.image.owner),
                number: prepareInt(this.state.image.number),
                ids: prepareArray(this.state.image.ids),
                albumIds: prepareArray(this.state.image.albumIds),
            } : null,
            video: isExist(this.state.video) ? {
                id: this.state.video.objId,
                type: this.state.video.type,
                ownerId: prepareInt(this.state.video.owner),
                number: prepareInt(this.state.video.number),
                ids: prepareArray(this.state.video.ids),
                albumIds: prepareArray(this.state.video.albumIds),
            } : null
        }

        console.log('send body: ' + JSON.stringify(body))

        this.action(body)
    }

    deleteCommand() {
        var resp = BotApi.deleteCommand(this.state.id)

        this.props.app.go({ currentTarget: { dataset: { to: 'com_list' } } })
        this.props.app.setPopout(
            <Alert
                actions={[{
                    title: 'ОК',
                    mode: 'cancel',
                    autoclose: true,
                }]}
                onClose={() => this.props.app.setPopout(null)}
            >
                <p>{resp === 'error' ? 'Ошибка удаления команды!' : 'Команда удалена!'}</p>
            </Alert>
        )
    }

    changeField(e) {
        const n = e.currentTarget.name
        const v = e.currentTarget.value
        this.setState(ps => {
            if (v === '' || n === 'text' || !isNaN(v) || v === '-') {
                this.state[n] = v
            }
            return ps
        })
    }

    selectCommunity(e) {
        const n = e.currentTarget.name
        const v = e.currentTarget.value
        this.setState(ps => {
            ps.communities[n] = v === '' ? '' : Number(v)
            return ps
        })
    }

    addCommunity() {
        this.setState(ps => {
            ps.communities.push('')
            return ps
        })
    }

    deleteCommunity(e) {
        const index = e.currentTarget.dataset.index
        this.setState(ps => {
            ps.communities.splice(index, 1)
            return ps
        })
    }

    getCommunities() {
        return (
            <Group header={<Header>Подключение сообществ</Header>}>
                <FormLayout>
                    {this.state.communities.map((c, i) => (
                        <Cell asideContent={<Icon24Cancel data-index={i} onClick={this.deleteCommunity} />}>
                            <Select placeholder="Выберите сообщество" name={i} value={c} onChange={this.selectCommunity}>
                                {this.communsList.map(comm => <option value={comm.id}>{comm.name}</option>)}
                            </Select>
                        </Cell>
                    ))}
                    <Button size="s" before={<Icon24Add />} disabled={this.state.communities.length >= 10} onClick={this.addCommunity}>Добавить сообщество</Button>
                </FormLayout>
            </Group>
        )
    }

    render() {
        return (
            <Panel id={this.props.id}>
                <PanelHeader
                    left={<PanelHeaderButton onClick={this.props.go} data-to="com_list">
                        {osName === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
                    </PanelHeaderButton>}
                >{this.header}</PanelHeader>
                <FormLayout>
                    <Group>
                        <Div><Input type="text" placeholder="Название команды" value={this.state.comName} onChange={this.changeName} /></Div>
                    </Group>
                    <Separator />
                    <Group header={<Header>Ответ команды</Header>}>
                        <Text cc={this} item={this.state.text} />
                        <Post cc={this} item={this.state.post} />
                        <Media cc={this} item={this.state.image} />
                        <Media cc={this} item={this.state.video} />
                    </Group>
                </FormLayout>
                <Group>
                    <ComItemButton cc={this} item={this.state.text} />
                    <ComItemButton cc={this} item={this.state.post} />
                    <ComItemButton cc={this} item={this.state.image} />
                    <ComItemButton cc={this} item={this.state.video} />
                </Group>
                <Separator />
                <Div>
                    {this.getCommunities()}
                    <Group header={<Header>Дополнительные настройки</Header>}>
                        <FormLayout>
                            <Input top="Приоритет" type="text" name='priority' value={this.state.priority} onChange={this.changeField} />
                        </FormLayout>
                    </Group>
                </Div>
                <Separator />
                <Div>
                    <Group>
                        <Div>
                            <Button size="xl" onClick={this.validate}>{this.actionText}</Button>
                        </Div>
                    </Group>
                    <Group>
                        <Div hidden={this.state.id == 0}>
                            <Button style={{ color: 'red' }} size="l" mode='tertiary' onClick={this.deleteCommand}>Удалить команду</Button>
                        </Div>
                    </Group>
                </Div>
            </Panel>
        )
    }
}

export default ComPanel
