import React from 'react'
import Group from '@vkontakte/vkui/dist/components/Group/Group'
import Header from '@vkontakte/vkui/dist/components/Header/Header'
import Icon28Cancel from '@vkontakte/icons/dist/28/cancel_outline'

class ComItem extends React.Component {
    constructor(props) {
        super()
        this.state = {
            item: props.item
        }
        this.selectItemType = this.selectItemType.bind(this)
        this.setFieldHidden = this.setFieldHidden.bind(this)
        this.changeField = this.changeField.bind(this)
        this.deleteItem = this.deleteItem.bind(this)
        this.changeTextField = this.changeTextField.bind(this)
    }

    selectItemType(e) {
        const v = e.target.value
        this.props.cc.setState(ps => {
            this.state.item.type = v.length == 0 ? null : v
            return ps
        })
    }

    setFieldHidden(types) {
        for (var t in types) {
            if (types[t] === this.state.item.type) {
                return false
            }
        }
        return true
    }

    changeField(e) {
        const n = e.currentTarget.name
        const v = e.currentTarget.value
        this.props.cc.setState(ps => {
            if (v === '' || v === '-' || n === 'text' || !isNaN(v)) {
                this.state.item[n] = v
            }
            return ps
        })
    }

    changeTextField(e) {
        const n = e.currentTarget.name
        const v = e.currentTarget.value
        this.props.cc.setState(ps => {
                this.state.item[n] = v
            return ps
        })
    }

    deleteItem() {
        this.props.cc.setState(ps => {
            this.state.item.status = false
            return ps
        })
    }

    render() {
        return (
            <Group
                hidden={!this.state.item.status}
                header={<Header mode="secondary" aside={<Icon28Cancel onClick={this.deleteItem} />}>{this.state.item.rusName}</Header>}>
                {this.fields()}
            </Group>
        )
    }
}

export default ComItem
