import React from 'react'
import Div from '@vkontakte/vkui/dist/components/Div/Div'
import Button from '@vkontakte/vkui/dist/components/Button/Button'

class ComItemButton extends React.Component {
    constructor() {
        super()
        this.setItem = this.setItem.bind(this)
    }

    setItem() {
        this.props.cc.setState(ps => {
            ps[this.props.item.name].status = !ps[this.props.item.name].status
            return ps
        })
    }

    render() {
        return (
            <Div hidden={this.props?.item?.status}>
                <Button
                    size='m'
                    onClick={this.setItem}
                    mode='commerce'
                >
                    {`Добавить ${this.props.item.rusName}`}
                </Button>
            </Div>
        )
    }
}

export default ComItemButton
