class BotApi {
    static query(method, url, body) {
        const tempMethod = method === 'GET' ? method : 'POST'
        var xhr = new XMLHttpRequest()
        xhr.open(tempMethod, `https://pavlentygood.fvds.ru/query.php?method=${method}&url=${encodeURIComponent('http://91.240.84.91:8124/roybot/' + url)}&auth_url=${encodeURIComponent(document.location.href)}`, false)
        if (method === 'POST' || method === 'PUT') {
            xhr.setRequestHeader('Content-Type', 'application/json')
            xhr.send(JSON.stringify(body))
        } else {
            xhr.send()
        }
        if (xhr.responseText === 'auth_error') {
            return xhr.responseText
        }
        if (xhr.responseText.length > 0) {
            return xhr.responseText === 'error' ? 'error' : JSON.parse(xhr.responseText)
        }
    }

    static getUser(id) {
        return this.query('GET', `app_info?userId=${id}`)
    }

    static getAvailableCommunities(userId) {
        return this.query('GET', `available_communities?userId=${userId}`)
    }

    static getUserCommands(userId) {
        return this.query('GET', `simple_command?userId=${userId}`)
    }

    static addCommand(body) {
        return this.query('POST', 'simple_command', body)
    }

    static updateCommand(body) {
        return this.query('PUT', 'simple_command', body)
    }

    static deleteCommand(id) {
        return this.query('DELETE', `simple_command?id=${id}`)
    }

    static vk(url) {
        var addr = `https://api.vk.com/method/${url}&v=5.81&access_token=81025aad81025aad81025aad7c8173fe038810281025aaddfadd240e8df843a9e48e0fe`
        var xhr = new XMLHttpRequest()
        xhr.open('GET', `https://pavlentygood.fvds.ru/query.php?method=GET&url=${encodeURIComponent(addr)}&auth_url=${encodeURIComponent(document.location.href)}`, false)
        xhr.send()
        if (xhr.responseText === 'auth_error') {
            return xhr.responseText
        }
        if (xhr.responseText.length > 0) {
            return xhr.responseText === 'error' ? 'error' : JSON.parse(xhr.responseText).response
        }
    }
}

export default BotApi
