import React from 'react'
import { platform, IOS } from '@vkontakte/vkui'
import Alert from '@vkontakte/vkui/dist/components/Alert/Alert'
import BotApi from '../../BotApi'
import ComPanel from './ComPanel'

class ComEditor extends ComPanel {
    constructor(props) {
        super(props)
        this.header = 'Просмотр команды'
        this.actionText = 'Сохранить'

        const prepareInt = a => a == 0 ? '' : a + ''

        const com = props.app.command

        console.log('receive com: ' + JSON.stringify(com))

        this.state.id = com.id
        this.state.comName = com.name
        this.state.priority = prepareInt(com.priority)
        com.communityLinks.forEach(cl => this.state.communities.push(cl.community.id))
        if (com.text != null && com.text != '') {
            this.state.text.status = true
            this.state.text.text = com.text
        }
        if (com.post != null) {
            this.state.post.status = true
            this.state.post.objId = com.post.id
            this.state.post.type = com.post.type
            this.state.post.owner = 'vk.com/' + (com.post.ownerId > 0 ? `id${com.post.ownerId}` : `club${-com.post.ownerId}`)
            this.state.post.reference = `vk.com/wall${com.post.ownerId}_${com.post.postId}`
            this.state.post.number = prepareInt(com.post.number)
            this.state.post.withPinned = com.post.withPinned
        }
        const parseMediaItem = (item, src, t) => {
            if (src == null) {
                return
            }
            item.status = true
            item.objId = src.id
            item.type = src.type
            item.owner = prepareInt(src.ownerId)
            item.number = prepareInt(src.number)
            item.ids = src.ids.split(' ').map(id => `vk.com/${t}${src.ownerId}_${id}`)
            item.albumIds = src.albumIds.split(' ').map(id => `vk.com/album${src.ownerId}_${id}`)
        }
        parseMediaItem(this.state.image, com.image, 'photo')
        parseMediaItem(this.state.video, com.video, 'video')
    }

    updateCommand(body) {
        var resp = BotApi.updateCommand(body)

        this.props.app.go({currentTarget: { dataset: { to: 'com_list'}}})
        this.props.app.setPopout(
            <Alert
                actions={[{
                    title: 'ОК',
                    mode: 'cancel',
                    autoclose: true,
                }]}
                onClose={() => this.props.app.setPopout(null)}
            >
                <p>{resp === 'error' ? 'Ошибка сохранения изменений!' : 'Изменения сохранены!'}</p>
            </Alert>
        )
    }

    action(body) {
        this.updateCommand(body)
    }
}

export default ComEditor
